<template>
    <div class="Iconcontent">
        <div class="content"> 
            <template v-for="(item, index) in iconData">
                <ul :key="index">
                    <li @click="goState(item.title)">
                        <div class="Iconimg">
                            <img :src="item.url" alt="item.title">
                        </div>
                        <div class="Icontext">
                            <h2 class="title">{{item.title}}<span>{{item.num}}</span></h2>
                            <p class="content">{{item.content}}</p>
                        </div>
                        <div class="Iconicon">
                            <i class="fa fa-angle-right"></i>
                        </div>
                    
                    </li>
                </ul>
            </template>
        </div>

    </div>
</template>

<style lang="less" scoped>
    .Iconcontent {
        .content {
            ul {
                border-bottom: 0.1rem solid lightgray;

                li {
                    padding: 1rem;
                    display: flex;

                    .Iconimg {
                        flex-basis: 20%;

                        img {
                            width: 5rem;
                            height: 5rem;
                        }
                    }

                    .Icontext {
                        flex-basis: 300%;
                        margin: 0 2rem;
                        display: flex;
                        flex-wrap: wrap;
                        align-content: space-evenly;

                        .title {
                            flex-basis: 100%;
                            font-size: 1.5rem;

                            span {
                                font-size: 1rem;
                                border: 0.1rem solid #8d8d8d;
                                padding: 0.2rem 0.5rem;
                                margin-left: 1rem;
                                border-radius: 0.3rem;
                                color: #8d8d8d;
                            }
                        }

                        .content {
                            flex-basis: 100%;
                            font-size: 1.2rem;
                            letter-spacing: 0.2rem;
                        }
                    }

                    .Iconicon {
                        flex-basis: 100%;
                        text-align: right;
                        align-self: center;
                        font-size: 1.8rem;
                        color: rgb(168, 165, 165);
                    }
                }
            }
        }
    }
</style>

<script>
    export default {
        props: ['iconData'],
        name: 'Iconcontent',
        methods: {
            goState(title) {
                this.$router.push({name:'iconAbout',params:{
                    result:this.iconData,
                    title
                }})
            }
        },
    }
</script>